:root{
  --background:linear-gradient(120deg, #a81010,#653445,#1bffce);
  --formbg: transparent;
  --text: #e2e2e2;
}

.App {
  text-align: center;
  padding-bottom: 100px;
  background-image: var(--background);
  background-attachment: fixed;
  min-height: 100vh;
}

.background-toggler{
  z-index: 99;
  display: flex;
  justify-content: flex-end;
  color: #e2e2e2;
  margin-left: center;
  position: fixed;
  bottom:20px;
  right: 20px;
}

.background-toggler select{
  border: 1px solid #e2e2e2;
  padding: 2px;
  border-radius: 2px;
}

ol{
  padding: 0;
  margin: 0;
}

ol:nth-of-type(1) {
  padding-top: 120px;
}

ol:nth-of-type(2) {
  padding-top: 40px;
}

span > small {
  font-size: 10px;
  color:#b1afadd7;
  position: relative;
  bottom: -5px;
  font-family: 'Gill Sans', 'Gill Sans MT', 'Trebuchet MS', sans-serif!important;
}

form{
  background-color: var(--formbg);
  display: flex;
  padding: 30px 5px;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  box-shadow: 0px 2px .2px .5px rgba(0,0,0,.1)
}

input:focus,button:focus,select:focus{
  outline: none;
}

form input,form button{
  padding: 10px 5px;
  border: 1px solid #e2e2e2;
  border-radius: 5px;
}
form input{
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
}

form button{
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
}

li{
  display: flex;
  max-width: 400px;
  justify-content: space-evenly;
  align-items: center;
  user-select: none;
  padding: 10px 5px;
  border: 1px solid #e2e2e2;
  border-top:none;
  list-style: none;
  margin: auto;
}

li:nth-of-type(1){
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-top: 1px solid #e2e2e2;
  box-shadow: 0px -5px .2px .5px rgba(0,0,0,.1), 0px 0px 1px 1px rgba(0,0,0,.1)
}

li:last-of-type{
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border-top:none;
  box-shadow: 0px 5px .2px .5px rgba(0,0,0,.1), 0px 0px 1px 1px rgba(0,0,0,.1)
}

li > span{
  flex: 5;
  text-align: left;
  padding: 0px 5px 0px 5px;
  font-family: monospace;
  font-size:14px;
  cursor: pointer;
  color: var(--text);
}

li > button{
  border: 0px solid #fff;
  color: #fff;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0px 2px 2px .5px rgba(0,0,0,.2);
  margin:0px 5px;
  cursor: pointer;
}

li > :nth-child(2){
  flex-basis: 20px;
  background: #269781;
}

li > :nth-child(3){
  flex-basis: 30px;
  background: #e24343;
}

li > .pending {
  flex-basis: 150px;
}

li > .completed {
  text-decoration: line-through;
  opacity: .8;
}


.list-toggler {
  border:1px solid  #d2d2d2;
  box-shadow: none;
  background: transparent!important;
  color: var(--text);
  text-decoration: none!important;
  padding: 2px 10px;
}

.list-toggler.active{
  background: #e2e2e2!important;
  color: #222222;
}